<script>
import { mapActions } from 'vuex'
import LineChart from '../components/LineChart'
export default {
  components: {
    LineChart
  },
  data () {
    return {
      history: '',
      statusklik: true,
      idKlik: '',
      details: '',
      dataChart: '',
      jmlDay: [],
      jmlDays: []
    }
  },
  computed: {
    getCountdayy () {
      return this.jmlDay
    }
  },
  methods: {
    ...mapActions({
      gethitory: 'checkout/getHistorCheckout',
      detail: 'checkout/getDetailhistoryChekout',
      countDay: 'checkout/thisCount'
    }),
    fillData () {
      this.datacollection = {
        labels: ['total pendapatan'],
        datasets: [
          {
            label: 'pendapatan hari ini',
            backgroundColor: '#f87979',
            data: [0, 20000]
          }
        ]
      }
    },
    getRandomInt () {
      return Math.floor(Math.random() * (50 - 5 + 1)) + 5
    },
    klikinfo (id) {
      this.idKlik = id
      this.statusklik = false
      this.detail(id).then(ress => {
        this.details = ress
      })
    },
    closebtn () {
      this.statusklik = true
    }
  },
  mounted () {
    this.fillData()
    this.gethitory().then(ress => {
      this.history = ress
      this.history.map(dt => {
        this.gethitory = dt
      })
    })
    this.countDay().then(dt => {
      this.jmlDay = parseInt(dt[0].totalhariini)
    })
  }

}
</script>

