<template>
<div>
    <b-modal  :hide-footer="true" id="modal-3">
       <div  class="modal-header">
            <div class="mdl1">
              <p class="font-weight-bold">Checkout</p>
              <p>Cashier : Pevita Pearce</p>
            </div>
            <div class="mdl2">
              <p class="font-weight-bold">Receipt no: #010410919</p>
            </div>
          </div>
          <div class="modal-body">
             <div v-for="(item,index) in  asu.product " :key="index"  class="mdl-body">
              <div class="list-item">{{ item.title }} {{item.qty}}x</div>
              <div class="price">{{(item.harga * item.qty).toLocaleString()}}</div>
            </div>
            <div class="mdl-body">
              <div></div>
              <div class="price">total: {{totalPrice.toLocaleString()}}</div>
            </div>
            <div class="font-weight-bold">Payment Cash</div>
            <div class="mdl-button">
              <button @click="printPdf()" class="print">Print</button>
              <span class="font-weight-bold">or</span>
              <button class="print">Send Email</button>
            </div>
          </div>
  </b-modal>
</div>
</template>
<script>
import Jspdf from 'jspdf'
import { mapState, mapGetters } from 'vuex'
import 'jspdf-autotable'
export default {
  computed: {
    ...mapState({
      asu: 'cart'
    }),
    ...mapGetters({
      total: 'cart/getCart'
    }),
    totalPrice () {
      return this.total.reduce((a, b) => a + b.qty * b.harga, 0)
    }
  },
  methods: {
    printPdf () {
      const data = this.asu.product
      // eslint-disable-next-line no-unused-vars
      const col = ['no', 'produk', 'jumlah', 'harga']
      const col1 = ['                  ', '            ', '         ', 'total']
      // eslint-disable-next-line no-unused-vars
      const row = []
      const row1 = []
      console.log(JSON.parse(JSON.stringify(data)))
      data.map((item, index) => {
        const jumlahHarga = parseInt(item.harga * item.qty)
        const temp = [index, item.title, item.qty, jumlahHarga]
        row.push(temp)
        console.log(index)
      })
      const temp1 = ['', '', '', this.totalPrice]
      row1.push(temp1)
      const doc = new Jspdf()
      doc.autoTable({ html: '#table' })
      doc.autoTable(col, row, { startY: 10 })
      doc.autoTable(col1, row1, { startY: 60 })
      // const as = []
      // this.asu.product.map(item => {
      //   as.push([item.title, item.harga, this.totalPrice])
      // })
      // console.log(as)
      // doc.autoTable({
      //   head: [['kasir', 'produk', 'jumlah']],
      //   body: [
      //     ['pevita', `${item.title}`, `${this.totalPrice}`]
      //   ]
      // })
      doc.save('checkout.pdf')
      window.location.reload()
    }
  }

}
</script>

