<template>
  <footer class="fixed-bottom mt-5">
      <div class="footer-content">
        <div class="ftn" ><router-link to="/history"><img src="icon/clipboard.png" alt="" /></router-link></div>
        <div class="ftn" ><router-link to="/"><img src="icon/fork (1).png" alt="" /></router-link></div>
        <div v-if="role == 1" class="ftn"
        style="cursor: pointer;"
        ype="button"
        data-toggle="modal"
        v-b-modal.modal-2
        data-target="#modal2">
        <img src="icon/add.png" alt="" /></div>
        <div @click="togle" class="ftn" ><a style=" text-decoration: none;" href="#">cart <span>{{total.length}}</span> </a></div>
        <div  data-toggle="modal"
        data-target="#modal2" @click="Onlogout" class="ftn" ><h4 class="btn btn-primary btn-sm">logout</h4></div>
      </div>
      <Add />
    </footer>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import Add from './modal/Add'
export default {
  components: {
    Add
  },
  data () {
    return {
      role: localStorage.getItem('role')
    }
  },
  computed: {
    ...mapGetters({
      total: 'cart/getCart'
    })
  },
  methods: {
    ...mapActions({
      togle: 'cart/togleslide'
    }),
    Onlogout () {
      localStorage.removeItem('token')
      localStorage.removeItem('refreshToken')
      localStorage.removeItem('role')
      localStorage.removeItem('email')
      window.location.reload()
    }
  }
}
</script>

<style scoped>
span{
 background: blue;
   border-radius: 0.8em;
  -moz-border-radius: 0.8em;
  -webkit-border-radius: 0.8em;
  color: #ffffff;
  display: inline-block;
  font-weight: bold;
  line-height: 1em;
  margin-right: 15px;
  text-align: center;
  width: 1em;
}

</style>
