import Axios from 'axios'
const state = () => {
  return {
  }
}
const getters = {
}

const mutations = {
}
const actions = {
  insertCheckout ({ commit }, data) {
    return new Promise((resolve, reject) => {
      Axios.post('http://18.208.165.238:5600/api/transaksi', data).then((ress) => {
        resolve(ress.data)
      })
    })
  },
  getHistorCheckout ({ commit }) {
    return new Promise((resolve, reject) => {
      Axios.get('http://18.208.165.238:5600/api/history').then(dt => {
        resolve(dt.data)
      })
    })
  },
  getDetailhistoryChekout ({ commit }, id) {
    return new Promise((resolve, reject) => {
      Axios.get(`http://18.208.165.238:5600/api/checkout/${id}`).then(dt => {
        resolve(dt.data)
      })
    })
  },
  thisCount ({ commit }, tanggal) {
    return new Promise((resolve, reject) => {
      Axios.get('http://18.208.165.238:5600/api/thisdaycount/2020-10-26').then(ress => {
        resolve(ress.data)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
